@charset "UTF-8";
body {
  margin: 0;
  padding: 0 15px;
  background-color: skyblue;
  font-family: Myriad Pro、Myriad、Helvetica Neue、Helvetica、Liberation Sans、Arial、sans-serif;
}

body > header {
  width: 80%;
  margin: 0 auto;
}

body h2 {
  background-color: #f55053;
  height: 100px;
  margin-top: 10px;
  color: #fff;
}

body header + h2 {
  margin-top: 0;
  background-color: #f90;
}

body h2 ~ h2 {
  border-top: 1px dashed #ccc;
}

h1 {
  color: #f90;
  font-size: 20px;
}

h2 {
  color: #f55053;
  font-size: 20px;
}

h2 a {
  color: aliceblue;
}

h2 a:hover {
  color: blue;
}

header + h2 {
  font-size: 30px;
}

h1,
h2,
h3 {
  margin-left: 5px;
}

body.fireFox h1, body.fireFox
h2, body.fireFox
h3 {
  color: blue;
}
